: rgb(109, 109, 109);\n  font-size: 28px;\n}\n.tool-earning-operation-box[data-v-0c1d95ce]{\n  flex-direction: row;\n  align-items: center;\n}\n.tool-earning-box[data-v-0c1d95ce]{\n  flex-direction: row;\n  align-items: center;\n  margin-right: 30px;\n}\n.tool-earning-box-status[data-v-0c1d95ce]{\n  font-size: 28px;\n  color: rgb(0, 0, 0);\n}\n.tool-earning-box-num[data-v-0c1d95ce]{\n  margin-left: 10px;\n  font-size: 28px;\n  color: rgb(249, 52, 80);\n}\n.tool-operation-box[data-v-0c1d95ce]{\n  flex-direction: row;;\n  align-items: center;\n  justify-content: flex-end;\n  margin-right: 6px;\n}\n.tool-operation-box-status[data-v-0c1d95ce]{\n  margin-right: 8px;\n  font-size: 28px;\n  color: rgb(0, 0, 0);\n}\n.tool-operation-box-button-online[data-v-0c1d95ce]{\n  width: 98px;\n  height: 38px;\n  border-radius: 100px;\n  flex-direction: row;\n  align-items: center;\n  background-color: #F63754\n}\n.tool-operation-box-button-offline[data-v-0c1d95ce]{\n  width: 98px;\n  height: 38px;\n  border-radius: 100px;\n  flex-direction: row;\n  align-items: center;\n  background-color: #D5D5D5\n}\n.tool-operation-box-button-icon-left[data-v-0c1d95ce]{\n  background-color: rgb(255, 255, 255);\n  margin-left: 4px;\n  width: 30px;\n  height: 30px;\n  border-radius: 15px;\n}\n.tool-operation-box-button-icon-right[data-v-0c1d95ce]{\n  background-color: rgb(255, 255, 255);\n  margin-left: 64px;\n  width: 30px;\n  height: 30px;\n  border-radius: 15px;\n}\n/*end*/\n\n/*start 加载更多*/\n.loading[data-v-0c1d95ce] {\n  width: 750px;\n  height: 60px;\n  background-color: transparent;\n  align-items: center;\n  justify-content: center;\n}\n/*end*/\n", "", {"version":3,"sources":["/./src/pages/hot-recommend-weex/hot-recommend-weex.vue?3d2b8fc2"],"names":[],"mappings":";AAmEA;CAEA;AACA;EACA,iBAAA;EACA,QAAA;EACA,SAAA;CACA;;AAEA,gBAAA;AACA;EACA,oBAAA;EACA,wBAAA;EACA,+BAAA;EACA,QAAA;EACA,SAAA;EACA,kBAAA;EACA,iBAAA;CACA;AACA;EACA,oBAAA;CACA;AACA;EACA,aAAA;EACA,cAAA;CACA;AACA;EACA,kBAAA;EACA,iBAAA;EACA,oBAAA;EACA,aAAA;CACA;AACA;EACA,SAAA;EACA,mBAAA;EACA,gBAAA;EACA,wBAAA;CACA;AACA;EACA,oBAAA;EACA,oBAAA;EACA,iBAAA;CACA;AACA;EACA,gBAAA;EACA,wBAAA;CACA;AACA;EACA,kBAAA;EACA,8BAAA;EACA,gBAAA;EACA,0BAAA;CACA;AACA;EACA,mBAAA;EACA,wBAAA;EACA,YAAA;EACA,aAAA;EACA,wBAAA;EACA,6BAAA;EACA,gCAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;EACA,kBAAA;CACA;AACA,OAAA;;AAEA,eAAA;AACA;EACA,iBAAA;EACA,kBAAA;EACA,aAAA;EACA,aAAA;CACA;AACA;EACA,aAAA;EACA,aAAA;CACA;AACA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,UAAA;EACA,oBAAA;EACA,oBAAA;EACA,+BAAA;EACA,mBAAA;EACA,kBAAA;CACA;AACA;EACA,oBAAA;EACA,oBAAA;EACA,kBAAA;CACA;AACA;EACA,0BAAA;EACA,gBAAA;CACA;AACA;EACA,oBAAA;EACA,oBAAA;CACA;AACA;EACA,oBAAA;EACA,oBAAA;EACA,mBAAA;CACA;AACA;EACA,gBAAA;EACA,oBAAA;CACA;AACA;EACA,kBAAA;EACA,gBAAA;EACA,wBAAA;CACA;AACA;EACA,oBAAA;EACA,oBAAA;EACA,0BAAA;EACA,kBAAA;CACA;AACA;EACA,kBAAA;EACA,gBAAA;EACA,oBAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;EACA,qBAAA;EACA,oBAAA;EACA,oBAAA;EACA,yBAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;EACA,qBAAA;EACA,oBAAA;EACA,oBAAA;EACA,yBAAA;CACA;AACA;EACA,qCAAA;EACA,iBAAA;EACA,YAAA;EACA,aAAA;EACA,oBAAA;CACA;AACA;EACA,qCAAA;EACA,kBAAA;EACA,YAAA;EACA,aAAA;EACA,oBAAA;CACA;AACA,OAAA;;AAEA,cAAA;AACA;EACA,aAAA;EACA,aAAA;EACA,8BAAA;EACA,oBAAA;EACA,wBAAA;CACA;AACA,OAAA","file":"hot-recommend-weex.vue","sourcesContent":["<template>\n  <div>\n    <scroller class=\"main-list\" :style=\"{ height: listHeight + 'px' }\">\n      <div v-for=\"(item, itemIndex) in list\">\n        <div class=\"item\">\n          <!-- 商品信息 -->\n          <div class=\"item-header\">\n            <!-- <a :href=\"'/shop/sku/' + (item.skuId) + '.html?shopId=' + shopId\" class=\"item-header-box\"> -->\n            <div class=\"item-header-box\" @click=\"goodsDetailAction(item.skuId, shopId)\">\n                <image class=\"item-header-image\" :src=\"item.imgUrl + '.190x190.png'\"></image>\n                <div class=\"goods-info-box\">\n                  <text class=\"goods-info-box-title\" lines=\"2\">[{{item.brand}}]{{item.skuName}}</text>\n                  <div class=\"goods-info-price-box\">\n                    <text class=\"goods-info-price-box-price\">¥{{item.shopPrice}}</text>\n                    <text class=\"goods-info-price-box-original-price\">¥{{item.originalPrice}}</text>\n                  </div>\n                </div>\n              </div>\n              <!-- </a> -->\n\n              <!-- 分享 -->\n              <div v-if=\"!isWeb\" class=\"share-box\" @click=\"goodsShareAction(item)\">\n                <image class=\"share-box-icon\" src=\"//cdn1.showjoy.com/images/3b/3bfab532d3014c4cb72d6b81323844d3.png.50x50.png\"></image>\n              </div>\n            </div>\n            <!-- 工具栏 -->\n            <div v-if=\"shopId != 0\" class=\"item-main-footer\">\n              <image class=\"item-footer-tool-image\" src=\"//cdn1.showjoy.com/images/64/647fa222afaf478face1a980537e2f04.png\"></image>\n              <div class=\"item-footer\">\n                <div class=\"tool-box\">\n                  <text class=\"tool-box-online\">{{item.shelvesCountTip}}</text>\n                </div>\n                <!-- 上下架操作 -->\n                <div class=\"tool-earning-operation-box\">\n                  <div class=\"tool-earning-box\">\n                    <text class=\"tool-earning-box-status\">收益:</text>\n                    <text class=\"tool-earning-box-num\">{{item.commission}}</text>\n                  </div>\n                  <div  v-if=\"item.select == true\" class=\"tool-operation-box\">\n                    <text class=\"tool-operation-box-status\" >已上架</text>\n                    <div class=\"tool-operation-box-button-online\" @click=\"goodsStatusOperationOfflineAction(item)\">\n                      <div class=\"tool-operation-box-button-icon-right\"></div>\n                    </div>\n                  </div>\n                  <div  v-if=\"item.select == false\" class=\"tool-operation-box\">\n                    <text class=\"tool-operation-box-status\" >未上架</text>\n                    <div class=\"tool-operation-box-button-offline\" @click=\"goodsStatusOperationOnlineAction(item)\">\n                      <div class=\"tool-operation-box-button-icon-left\"></div>\n                    </div>\n                  </div>\n                </div>\n              </div>\n            </div>\n\n          </div>\n      </div>\n\n      <!-- 加载更多 -->\n      <loading class=\"loading\" @loading=\"loadItems\" :display=\"loadingStatus\" style=\"background-color: transparent\">\n        <loading-indicator style=\"width: 50px; height: 50px; color: #ccc;\"></loading-indicator>\n      </loading>\n    </scroller>\n  </div>\n</template>\n\n<style scoped>\n\n.main-list{\n\n}\n.item{\n  margin-top: 10px;\n  left: 0;\n  right: 0;\n}\n\n/*start 商品信息样式*/\n.item-header{\n  flex-direction: row;\n  align-items: flex-start;\n  justify-content: space-between;\n  left: 0;\n  right: 0;\n  margin-left: 40px;\n  margin-top: 30px;\n}\n.item-header-box{\n  flex-direction: row;\n}\n.item-header-image{\n  width: 190px;\n  height: 190px;\n}\n.goods-info-box{\n  margin-left: 14px;\n  margin-top: 30px;\n  margin-bottom: 20px;\n  width: 390px;\n}\n.goods-info-box-title{\n  lines: 2;\n  color:rgb(0, 0, 0);\n  font-size: 32px;\n  text-overflow: ellipsis;\n}\n.goods-info-price-box{\n  flex-direction: row;\n  align-items: center;\n  margin-top: 36px;\n}\n.goods-info-price-box-price{\n  font-size: 28px;\n  color: rgb(249, 52, 80);\n}\n.goods-info-price-box-original-price{\n  margin-left: 20px;\n  text-decoration: line-through;\n  font-size: 24px;\n  color: rgb(158, 158, 158);\n}\n.share-box{\n  align-self: center;\n  justify-content: center;\n  width: 96px;\n  height: 72px;\n  background-color: black;\n  border-top-left-radius: 48px;\n  border-bottom-left-radius: 48px;\n}\n.share-box-icon{\n  width: 50px;\n  height: 50px;\n  margin-left: 10px;\n}\n/*end*/\n\n/*start 工具栏样式*/\n.item-main-footer{\n  margin-top: 10px;\n  margin-left: 20px;\n  height: 64px;\n  width: 690px;\n}\n.item-footer-tool-image{\n  height: 64px;\n  width: 690px;\n}\n.item-footer{\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n  position: absolute;\n  padding-top: 13px;\n}\n.tool-box{\n  flex-direction: row;\n  align-items: center;\n  margin-left: 40px;\n}\n.tool-box-online{\n  color: rgb(109, 109, 109);\n  font-size: 28px;\n}\n.tool-earning-operation-box{\n  flex-direction: row;\n  align-items: center;\n}\n.tool-earning-box{\n  flex-direction: row;\n  align-items: center;\n  margin-right: 30px;\n}\n.tool-earning-box-status{\n  font-size: 28px;\n  color: rgb(0, 0, 0);\n}\n.tool-earning-box-num{\n  margin-left: 10px;\n  font-size: 28px;\n  color: rgb(249, 52, 80);\n}\n.tool-operation-box{\n  flex-direction: row;;\n  align-items: center;\n  justify-content: flex-end;\n  margin-right: 6px;\n}\n.tool-operation-box-status{\n  margin-right: 8px;\n  font-size: 28px;\n  color: rgb(0, 0, 0);\n}\n.tool-operation-box-button-online{\n  width: 98px;\n  height: 38px;\n  border-radius: 100px;\n  flex-direction: row;\n  align-items: center;\n  background-color: #F63754\n}\n.tool-operation-box-button-offline{\n  width: 98px;\n  height: 38px;\n  border-radius: 100px;\n  flex-direction: row;\n  align-items: center;\n  background-color: #D5D5D5\n}\n.tool-operation-box-button-icon-left{\n  background-color: rgb(255, 255, 255);\n  margin-left: 4px;\n  width: 30px;\n  height: 30px;\n  border-radius: 15px;\n}\n.tool-operation-box-button-icon-right{\n  background-color: rgb(255, 255, 255);\n  margin-left: 64px;\n  width: 30px;\n  height: 30px;\n  border-radius: 15px;\n}\n/*end*/\n\n/*start 加载更多*/\n.loading {\n  width: 750px;\n  height: 60px;\n  background-color: transparent;\n  align-items: center;\n  justify-content: center;\n}\n/*end*/\n</style>\n\n<script>\n// 组件\nvar noList = require('components/weex-no-list/weex-no-list');\nvar tipOrderClose = require('components/weex-tip-order-close/weex-tip-order-close');\nvar tipLogistic = require('components/weex-tip-logistic/weex-tip-logistic');\n\n// modules\nvar stream = weex.requireModule('stream');\nvar shopModal = weex.requireModule('shopModal');\nvar shopBase = weex.requireModule('shopBase');\n\nmodule.exports =  {\n  data: function () {\n    return {\n      listHeight: 0,\n      pageIndex: 1,\n      loadingStatus: 'hide',\n      pageSize: 10,\n      isWeb: false,\n      noData: false,\n      initData: false,\n       list: [\n      // {\n      //     \"commission\": \"99.90\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/a3/a38947bed236481f848af7ec663351e0.png\",\n      //     \"inventory\": 17,\n      //     \"originalPrice\": 1200,\n      //     \"select\": false,\n      //     \"shelvesCount\": 931,\n      //     \"shopPrice\": 999,\n      //     \"skuId\": 7335,\n      //     \"skuName\": \"邂逅柔情女士淡香水（预售5天） 100.0ml\"\n      // },\n      // {\n      //     \"commission\": \"9.80\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/a9/a9794f0ef5f347d083dbc436c86da8f6.png\",\n      //     \"inventory\": 33,\n      //     \"originalPrice\": 108,\n      //     \"select\": false,\n      //     \"shelvesCount\": 935,\n      //     \"shopPrice\": 98,\n      //     \"skuId\": 148026,\n      //     \"skuName\": \"杜蕾斯27只高潮情动组合 \"\n      // },\n      // {\n      //     \"commission\": \"6.80\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/9d/9d0e8914be744de4a7d344e1a1ec97b8.png\",\n      //     \"inventory\": 0,\n      //     \"originalPrice\": 158,\n      //     \"select\": true,\n      //     \"shelvesCount\": 4349,\n      //     \"shopPrice\": 68,\n      //     \"skuId\": 15815,\n      //     \"skuName\": \"鲜亮焕采泡沫洁面乳 30.0ml\"\n      // },\n      // {\n      //     \"commission\": \"6.50\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/bd/bd917033be2e49908f9e3bbe2a3a2710.png\",\n      //     \"inventory\": 7571,\n      //     \"originalPrice\": 288,\n      //     \"select\": false,\n      //     \"shelvesCount\": 1896,\n      //     \"shopPrice\": 65,\n      //     \"skuId\": 105692,\n      //     \"skuName\": \"红石榴美肤套装【赠价值59元卡姿兰大眼睛持久液体眼线笔】 320.0ml\"\n      // },\n      // {\n      //     \"commission\": \"6.80\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/05/0531885951894362af155ea6d8deea34.png\",\n      //     \"inventory\": 45,\n      //     \"originalPrice\": 298,\n      //     \"select\": false,\n      //     \"shelvesCount\": 558,\n      //     \"shopPrice\": 68,\n      //     \"skuId\": 149209,\n      //     \"skuName\": \"粉色零束缚无钢圈内衣 \"\n      // },\n      // {\n      //     \"commission\": \"28.20\",\n      //     \"imgUrl\": \"//cdn1.showjoy.com/images/4b/4b083bba25b941b2b6dcb410e9c25376.png\",\n      //     \"inventory\": 0,\n      //     \"originalPrice\": 288,\n      //     \"select\": false,\n      //     \"shelvesCount\": 827,\n      //     \"shopPrice\": 188,\n      //     \"skuId\": 149358,\n      //     \"skuName\": \"高档加绒自发热亲肤塑身保暖内衣套装（均码） \"\n      // }\n      ],\n      mainTitle: '',\n      subHead: '',\n      shopId: ''\n    }\n  },\n\n  created: function(){\n    shopBase.userTrack({key:'home_hot_recommend', params:{}});\n    var self = this;\n    // 设置\n    self.isWeb = weex.config.env.platform == 'Web' ? true : false;\n    // 渲染首屏数据\n    self.requestHotRcommendData();\n    // 设置list的高度\n    this.setListHeight();\n  },\n\n  mounted: function () {\n    var self = this;\n  },\n\n  methods: {\n    //适配列表\n    setListHeight: function () {\n      let self = this;\n      let deviceHeight = parseInt(weex.config.env.deviceHeight);\n      let rate = weex.config.env.deviceWidth / 750;\n      let deviceScale = weex.config.env.scale;\n\n     if (weex.config.env.platform.toLowerCase() === 'web') {\n        self.listHeight = parseInt(weex.config.env.deviceHeight / rate);\n      } else {\n        self.listHeight = (deviceHeight - (64 * deviceScale)) / rate;\n      }\n    },\n    //加载更多\n    loadItems: function () {\n      let self = this;\n      self.loadingStatus = 'show';\n      self.pageIndex++;\n      // shopBase.showLoading();\n      self.request(self.pageIndex, function () {\n        // shopBase.hideLoading();\n        self.loadingStatus = 'hide';\n\n        // weex-vue-render list bug hack\n        if (self.isWeb) {\n          document.querySelector('.weex-list-inner').style.transform = 'translate3d(0px, 0px, 0px)';\n        }\n      });\n    },\n    //网络请求\n    request: function (page, cb) {\n      let self = this;\n      stream.fetch({\n        method: 'GET',\n        url: \"/api/shop/activityExposure/allSku?type=\"+ weex.config.type +\"&page=\" + page + \"&pageSize=\" + self.pageSize,\n        type: 'json'\n      }, function (response) {\n        shopBase.hideLoading();\n        if (response.ok) {\n          if (response.data.isSuccess) {\n            self.initData = true;\n            if (response.data.data.skuInfos != null) {\n              response.data.data.skuInfos.forEach(function (item, i) {\n                item.shopPrice = parseFloat(item.shopPrice).toFixed(2);\n                item.originalPrice = parseFloat(item.originalPrice).toFixed(2);\n                item.shelvesCountTip = self.goodsOnlineNumHandle(item.shelvesCount);\n                item.commission = parseFloat(item.commission).toFixed(2);\n              });\n              self.list = self.list.concat(response.data.data.skuInfos);\n            }\n            self.mainTitle = response.data.data.mainTitle;\n            self.subHead = response.data.data.subHead;\n            self.shopId = response.data.data.shopId;\n            shopBase.setTitle(self.mainTitle);\n\n            if (response.data.data.skuInfos == null && page != 1) {\n              shopModal.toast({ message: '最后一页啦', \"duration\": 2 });\n              self.pageIndex--;\n            }\n            cb(response.data.data.length);\n          } else {\n            shopModal.toast({ message: response.data.msg, \"duration\": 2 });\n            self.pageIndex--;\n          }\n        } else {\n          shopModal.toast({ message: '被挤爆啦! 请稍后再试~', \"duration\": 2 });\n          self.pageIndex--;\n        }\n      }, function (response) {});\n    },\n    requestHotRcommendData: function(){\n      let self = this;\n      shopBase.showLoading();\n      self.request(self.pageIndex, function (length) {\n        //分享页面\n        self.pageShareAction();\n        if (length === 0) {\n          self.noData = true;\n        }\n      });\n    },\n    //上架数量处理\n    goodsOnlineNumHandle: function(num){\n      var result;\n      if(num < 10000){\n        result = '已被'+ num +'位店主上架'\n      }else{\n        var miriadeLineCount = num / 10000\n        result = '已被'+ miriadeLineCount.toFixed(1)+'万位店主上架'\n      }\n      return result\n    },\n    //页面分享\n    pageShareAction: function(){\n      let self = this;\n      //线上线下\n      shopBase.isOnline(function(data) {\n      if(data.data) {\n          shopBase.shareInfo({'title': self.mainTitle, 'desc':self.subHead, 'link':'http://shop.m.showjoy.com/shop/activityExposure/hotRecommendWeex.html?shopId=' + self.shopId + '&type='+ weex.config.type,  'imgUrl': 'http://cdn1.showjoy.com/images/fe/fec026f0c0d045ed8daaf845a7e8bbc9.png'});\n        } else {\n          shopBase.shareInfo({'title': self.mainTitle, 'desc':self.subHead, 'link':'http://shop.m.showjoy.com/shop/activityExposure/hotRecommendWeex.html?shopId=' + self.shopId + '&type='+ weex.config.type,  'imgUrl': 'http://cdn1.showjoy.com/images/fe/fec026f0c0d045ed8daaf845a7e8bbc9.png'});\n        }\n      });\n    },\n    //商品分享\n    goodsShareAction: function(skuInfo){\n        shopBase.userTrack({key:'home_hot_recommend_detail_share', params:{}});\n        shopBase.shareDetail(skuInfo);\n    },\n    //商品详情\n    goodsDetailAction: function(skuId, shopId){\n      shopBase.userTrack({key:'home_hot_recommend_detail', params:{}});\n      let url = '/shop/sku/' + skuId + '.html?shopId=' + shopId;\n      shopBase.openUrl(url);\n    },\n    //上架操作\n    goodsStatusOperationOnlineAction: function(skuId){\n      let self = this;\n      self.requestOnlineGoods(skuId);\n    },\n    //下架操作\n    goodsStatusOperationOfflineAction: function(skuId){\n      let self = this;\n      self.requestOfflineGoods(skuId);\n    },\n    //上架请求\n    requestOnlineGoods: function (item) {\n      let self = this;\n      stream.fetch({\n        method: 'GET',\n        url: \"/api/shop/shopProduct/select?type=3&skuId=\" + item.skuId,\n        type: 'json'\n      }, function (response) {\n        if (response.ok) {\n          if (response.data.isSuccess) {\n              item.select = true;\n          } else {\n            if (response.data.msg != null) {\n              shopModal.toast({ message: response.data.msg, \"duration\": 2 });\n            }\n          }\n        } else {\n          shopModal.toast({ message: '被挤爆啦! 请稍后再试~', \"duration\": 2 });\n        }\n      });\n    },\n    //下架请求\n    requestOfflineGoods: function (item) {\n      let self = this;\n      item.select = false;\n      stream.fetch({\n        method: 'GET',\n        url: \"/api/shop/shopProduct/unselect?skuId=\" + item.skuId,\n        type: 'json'\n      }, function (response) {\n        if (response.ok) {\n          if (response.data.isSuccess) {\n            item.select = false;\n          } else {\n            if (response.data.msg != null) {\n              shopModal.toast({ message: response.data.msg, \"duration\": 2 });\n            }\n          }\n        } else {\n          shopModal.toast({ message: '被挤爆啦! 请稍后再试~', \"duration\": 2 });\n        }\n      });\n    },\n\n  }\n};\n</script>\n"],"sourceRoot":"webpack://"}]);

